
#app {
  width: 100%;
  height: 100%;
}

html, body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background: #000000;
}
.index, .bg-wrapper, .qrcode-wrapper {
  height: 100%;
  width: 100%;
}
.bg-wrapper, .qrcode-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.qrcode-wrapper {
  position: absolute;
  top: 0;
  left: 0;
}
.bg {
  width: 800px;
  height: 600px;
}
.qrcode {
  margin-top: 30px;
  margin-right: 10px;
  width: 200px;
  height: 200px;
  border: 20px solid #fff;
  border-radius: 40px;
  -webkit-mask-image: -webkit-radial-gradient(#ffffff, #ffffff, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.5), transparent, transparent);
}

.comments {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  overflow: hidden;
}

.comments .comment {
  display: inline-block;
  vertical-align: top;
  position: absolute;
  background-color: #21282b;
  border-radius: 4px;
  opacity: 0.8;
  font-size: 35px;
}
.comments .comment span {
    color: #fff;
    display: inline-block;
    padding: 5px 0px 5px 10px;
}
.comments .comment .user {
    font-weight: 200;
    color: #e7edf9;
    vertical-align: top;
}
.comments .comment .message {
    color: #ffbb3e;
    font-weight: 200;
    padding-right: 10px;
    padding-left: 5px;
    vertical-align: top;
}
.comments .comment.gold {
    font-size: 50px;
}
.comments .comment.gold .user {
      color: gold;
      word-break: keep-all;
}
.comments .comment.gold .message {
      word-break: break-all;
      max-width: 600px;
}
